import { h } from 'preact'
import styled from 'styled-components'

const Approach = styled.section`
    height: 40em;
    background-color: white;
`
const Container = styled.div`
    width: 80%;
    margin: auto;
    padding-top: 20em;
`

const Parent = styled.div`
    display: flex;
`

const FlexChild = styled.div`
    ${({ flex }) => flex && `flex: ${flex};`};
`

export default () => (
    <Approach>
        <Container>
            <Parent>
                <FlexChild flex="2 0 0">1</FlexChild>
                <FlexChild flex="1 0 0">2</FlexChild>
            </Parent>
        </Container>
    </Approach>
)



